.row{
  margin-left: 20px;
  color:white;
}

.row_posters{
    display:flex;
    overflow-x: scroll;
    overflow-y:hidden;
    padding: 20px;
}

.row_posters::-webkit-scrollbar {
    display:none;
}

.row_poster {
    object-fit: contain;
    width:100%;
    max-height: 100px;
    margin-right: 10px;
    transition: tranform 400ms;
}

.row_poster:hover{
    transform: scale(1.08);
}

.row_posterLarge {
    max-height: 250px;
}

.row_posterLarge:hover{
    transform: scale(1.09);
}